import { selectCartItems, selectCartItemsCount } from '../../../redux/cart/cart.selector';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { forwardRef } from 'react';
import MenuItem from './MenuItem';
import React from 'react';
import './styles.css';

const Menu = forwardRef(({ cartItems, cartCount, openItemDetails, list }, ref) => {
  return (
    cartCount !== 0 && cartItems.find((cartItem) => String(cartItem.id)[0] == 8) 
    ?
    <main className='menu-main' ref={ref}>
      <h1 className='menu-meal-time-title'>Breakfast</h1>
      <h1 className='menu-meal-time-title'>Lunch</h1>
      <h1 className='menu-meal-time-title'>Dinner</h1>
      {list.map((item) => (
        <MenuItem openItemDetails={openItemDetails} item={item} key={item.id} />
      ))}
    </main>
    :
    null
  )
});

const mapStateToProps = createStructuredSelector({
  cartItems: selectCartItems,
  cartCount: selectCartItemsCount,
});

export default connect(mapStateToProps)(Menu);
